<template>
    <div class="user--avatar">
       <div class="user--avatar__photo" v-if="user.user.imageUrl"><img v-bind:src="`${baseURL}${user.user.imageUrl}`"/></div>
       <div class="user--avatar__initials" v-else>{{ userNameInitials(user.user.fullName) }}</div>
       <div class="user--details">
        <h3>{{ user.user.fullName }}</h3>
        <div class="user--label" v-if="viewMode === 'DETAILED'">{{ user.userAccess }}</div>
       </div>
    </div>
</template>

<script>
import { BASE_AVATAR_URL } from "./../api.js";

export default {
  name: "User",
  props: ["view-mode", "user"],
  data() {
    return {
      baseURL: BASE_AVATAR_URL
    };
  },
  methods: {
    userNameInitials(fullName) {
      const names = fullName.split(" ");
      return names[0].charAt(0) + names[1].charAt(0);
    }
  }
};
</script>

<style lang="scss" scoped>
@import "./../scss/vars";

.user--avatar {
  display: grid;
  grid-template-columns: 30px 1fr min-content;
  grid-template-areas: "avatar label actions";
  grid-gap: spacing(4);
  margin-bottom: spacing(2);
}

.user--details {
  grid-area: label;
  align-content: center;
}

.user--label {
  @include label-small;
}

.user--avatar__photo,
.user--avatar__initials {
  grid-area: avatar;
  width: spacing(5);
  height: spacing(5);
  border-radius: 50%;
  overflow: hidden;
  user-select: none;

  img {
    object-fit: cover;
    width: spacing(5);
    height: spacing(5);
  }
}

.user--avatar__initials {
  background: linear-gradient(135deg, palette(blue), palette(green, 2));
  color: white;
  font-weight: 700;
  font-size: 20px;
  display: grid;
  justify-content: center;
  align-content: center;
}
</style>



